@font-face {
  font-family: Montserrat-Black;
  src: url(/static/media/Montserrat-Black.054ffd1b.ttf); 
}
@font-face {
  font-family: Montserrat-Regular;
  src: url(/static/media/Montserrat-Regular.f7213526.ttf); 
}

body {
  margin: 0;
  padding: 0;
  font-family: Montserrat-Regular;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
  @font-face {
    font-family: Montserrat-Black;
    src: url(/static/media/Montserrat-Black.054ffd1b.ttf); 
  }
  @font-face {
    font-family: Montserrat-Light;
    src: url(/static/media/Montserrat-Light.9e52b00d.ttf); 
  }
  
    .HomePage {
      text-align: center;
      background: #373373;
      min-height: 100vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    
    .Main-logo {
      -webkit-animation: Main-logo-spin infinite 20s linear;
              animation: Main-logo-spin infinite 20s linear;
      height: 40vmin;
    }
    
    .l1-txt1 {
      font-family: Montserrat-Black;
      font-size: 5rem;
      color: #eeeeee;
      line-height: 1.2;
      text-transform: uppercase;
    }
  
    .l1-txt2 {
      font-family: Montserrat-Light;
      font-size: 4rem;
      color: #eeeeee;
      line-height: 1.2;
    }

    .code{
      font-size: 6rem;
      color: #ffcc20ff;
    }
  

.QuestionPagePepper {
    padding-top: 100px;
    text-align: center;
    background: #eeeeee;
    min-height: 100vh;
    color: #373373;
}

.QuestionPagePepper .question-bar{
    display: flex;
    min-height: 6.625rem;
    padding: 3rem;
}
.QuestionPagePepper .question-bar__title-wrap {
    flex: 1 1;
    margin: 1.25rem 1rem;
}

.QuestionPagePepper .question-bar__title {
    font-size: 2.5rem;
    line-height: 1;
}

.QuestionPagePepper h1{
    font-size: 5rem;
}

.title-QuestionPagePepper{
    min-height: 6.625rem;
    flex: 1 1;
    margin: 1.25rem 1rem;
    box-shadow: inset 0 -0.1875rem rgba(0,0,0,0.15);
}

.timer{
    background-color: #373373;
    padding: 25px;
    border-radius: 50px;
    width: 90px;
    margin: 0 auto;
    color: #eeeeee
}

.QuizzPage {
    padding-top: 100px;
    text-align: center;
    background: #eeeeee;
    min-height: 100vh;   
    color: #373373;
}

.QuizzPage .question-bar{
    display: flex;
    box-shadow: inset 0 -0.1875rem rgba(0,0,0,0.15);
    min-height: 6.625rem;
}
.QuizzPage .question-bar__title-wrap {
    flex: 1 1;
    margin: 1.25rem 1rem;
}

.QuizzPage .question-bar__title {
    font-size: 2.5rem;
    line-height: 1;
}

.QuizzPage .question-panel{
    margin-top: 6rem;
}

.QuizzPage h1{
    font-size: 5rem;
}

  .AnswerPanelPepper .btn-answer-container{
    position: fixed;
    bottom: 0;
    width: 100%;
    margin: .625rem 0;
  }
  .AnswerPanelPepper .btn-answer{
    margin: 5px;
    padding: 8%;
    font-size: 2rem;
  }

  @font-face {
    font-family: Montserrat-Black;
    src: url(/static/media/Montserrat-Black.054ffd1b.ttf); 
  }
  @font-face {
    font-family: Montserrat-Light;
    src: url(/static/media/Montserrat-Light.9e52b00d.ttf); 
  }
  
    .ScorePage {
      text-align: center;
      background: #373373;
      min-height: 100vh;
      padding-top: 100px;
      align-items: center;
    }
    
    .score-logo {
      height: 20vmin;
    }

    h3{
      font-size: 3rem;
    }

    h4{
      font-size: 2.5rem;
    }
